import React from 'react'
import Styled from "styled-components"
import "./Skills.css"
const Wrapper = Styled.div`
padding:3rem 10rem 10rem 10rem;
width:100%;
transition:all .5s ease-in-out;

@media only screen and (max-width: 834px) {
       display:grid;
        place-items:center;
        padding:3rem 7rem 10rem 7rem;
        transition:all .5s ease-in-out;
    }
    @media only screen and (max-width: 400px) {
        padding:3rem 0rem 10rem 1rem;
        white-space: wrap;
        transition:all .5s ease-in-out;
    }
`

export const Skills = () => {
    return (
        <Wrapper>
            <h1 className="skills">Skills</h1>
            <div className="skills-icons">
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/html.svg'} alt="html.logo" name="html" />
                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/css.svg'} alt="css.logo" name="css" />

                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/javascript.svg'} alt="js.logo" name="javascript" />
                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/react.svg'} alt="react.logo" name="react" />
                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/redux.svg'} alt="redux.logo" name="redux" />
                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/materialui.svg'} alt="materialui.logo" name="materialui" />
                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/node.svg'} alt="node.logo" name="node" />
                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/express.svg'} alt="express.logo" name="express" />

                </div>
                <div className="skills-icons-individual-icon">
                    <img src={process.env.PUBLIC_URL + './Images/skills/mongodb.png'} alt="mongodb.logo" name="mongodb" />
                </div>
                
            </div>
        </Wrapper>
    )
}

