import React from 'react'
import "./SideBar.css"
import { GrClose } from "react-icons/gr";

export const SideBar = ({ handleClose }) => {
    return (
        <div className="sidebar-wrapper">
            <div className="sidebar-menu">
                <div className="sidebar-close-button"><GrClose size="34px" onClick={() => handleClose()} /></div>
                <div className="sidebar-menu-links">
                    <div>
                        <a href="#about" className="sidebar-links">
                            About
                        </a>
                    </div>
                    <div>
                        <a href="#project" className="sidebar-links">
                            Projects
                         </a>
                    </div>
                    <div>
                        <a href="#contact" className="sidebar-links">
                            Contact
                         </a>
                    </div>
                    <div>
                        <div>
                            <a className="sidebar-resume-button" href="#asd">
                                Resume
                             </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

