import React from 'react'
import Styled from "styled-components"
import { VscGithubAlt } from "react-icons/vsc";
import { HiOutlineExternalLink } from "react-icons/hi";

import "./Project.css"
const Wrapper = Styled.div`
padding:5% 15% 5% 15%;
width:100%;
transition:all .5s ease-in-out;
    @media only screen and (max-width: 834px) {
        padding:0rem 1rem 0rem 1rem;
        white-space: wrap;
        transition:all .5s ease-in-out;
        display:grid;
        place-items:center;
    } 
`

const Grid = Styled.div`
    display:grid;
    gap: 10px;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 100%;
    width:100%;
    margin-bottom:100px;
`
const GridAlt = Styled.div`
    display:grid;
    gap: 10px;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 100%;
    width:100%;
    margin-bottom:100px;
`
export const Project = () => {
    return (
        <Wrapper id="project">
            <h1 className="project-header">Project</h1>
            <div className="project">
                <Grid>
                    <div className="img-content">
                        <div className="img" style={{ background: `url(${process.env.PUBLIC_URL + "./project-images/movie.png"})` }}></div>
                    </div>
                    <div className="project-content">
                        <div className="project-content-wrapper">
                            <div className="project-title">
                                Movie Search
                        </div>
                            <div className="project-details">
                                The app in itself is a simple movie searching app. Clicking the movie list item it shows the detail of the movie and create your favorite List.
                       <br />
                                <em>Inspired from Netflix</em>
                            </div>
                            <div className="tech-stack">
                                <ul>
                                    <li>React</li>
                                    <li>MongoDB</li>
                                    <li>Express</li>
                                </ul>
                            </div>
                            <div className="project-links">
                                <VscGithubAlt size="24px" color="#fff" />
                                <HiOutlineExternalLink size="24px" color="#fff" />
                            </div>
                        </div>
                    </div>
                </Grid>
                <GridAlt>
                    <div className="project-content2">
                        <div className="project-content-wrapper">
                            <div className="project-title2">
                                Taza Chocolate
                        </div>
                            <div className="project-details">
                                User can look for different types of chocolate and add to there basket
                                and provide relevant details before checkout.
                       <br />
                                <em>Taza Chocolate Clone</em>
                            </div>
                            <div className="tech-stack2">
                                <ul>
                                    <li>React</li>
                                    <li>MongoDB</li>
                                    <li>Express</li>
                                </ul>
                            </div>
                            <div className="project-links2">
                                <VscGithubAlt size="24px" color="#fff" />
                                <HiOutlineExternalLink size="24px" color="#fff" />
                            </div>
                        </div>
                    </div>
                    <div className="img-content2">
                        <div className="img" style={{ background: `url(${process.env.PUBLIC_URL + "./project-images/shopping.png"})` }}></div>
                    </div>
                </GridAlt>
                <Grid>
                    <div className="img-content">
                        <div className="img" style={{ background: `url(${process.env.PUBLIC_URL + "./project-images/shine.png"})` }}></div>
                    </div>
                    <div className="project-content">
                        <div className="project-content-wrapper">
                            <div className="project-title">
                                Shine.com (Clone)
                        </div>
                            <div className="project-details">
                                Job searching platform. User can look for the desired job and can apply. The recruiter has access to the applicant's profile.
                       <br />
                                <em></em>
                            </div>
                            <div className="tech-stack">
                                <ul>
                                    <li>React</li>
                                    <li>Material-UI</li>
                                    <li>Heroku</li>
                                </ul>
                            </div>
                            <div className="project-links">
                                <VscGithubAlt size="24px" color="#fff" />
                                <HiOutlineExternalLink size="24px" color="#fff" />
                            </div>
                        </div>
                    </div>
                </Grid>
                <GridAlt>
                    <div className="project-content2">
                        <div className="project-content-wrapper">
                            <div className="project-title2">
                                The COVID Tracking Project
                        </div>
                            <div className="project-details">
                                A simple website to display current covid 19 cases in the world which include confirming cases, recover and total death.
                       <br />
                                <em></em>
                            </div>
                            <div className="tech-stack2">
                                <ul>
                                    <li>HTML</li>
                                    <li>CSS</li>
                                    <li>API</li>
                                </ul>
                            </div>
                            <div className="project-links2">
                                <VscGithubAlt size="24px" color="#fff" />
                                <HiOutlineExternalLink size="24px" color="#fff" />
                            </div>
                        </div>
                    </div>
                    <div className="img-content2">
                        <div className="img" style={{ background: `url(${process.env.PUBLIC_URL + "./project-images/covid.png"})` }}></div>
                    </div>
                </GridAlt>
            </div>
        </Wrapper>
    )
}

