import React from 'react'
import "./About.css"
import Styled from "styled-components"

const Wrapper = Styled.div`
padding:3rem 10rem 10rem 10rem;
width:100%;
transition:all .5s ease-in-out;

@media only screen and (max-width: 834px) {
       display:grid;
        place-items:center;
        padding:3rem 1rem 10rem 1rem;
        transition:all .5s ease-in-out;
    }
    @media only screen and (max-width: 400px) {
        padding:3rem 1rem 10rem 1rem;
        white-space: wrap;
        transition:all .5s ease-in-out;
    }
`

export const About = () => {
    return (
        <Wrapper id="about">
            <h1>About </h1>
            <div className="wrapper-about">
                <div className="about-data">
                    <div>
                    <p>Hello! I'm Sriniwas, a web developer from Bangalore,KAR</p>
                    <p>After graduation joined the Masai School. 6 months coding bootcamp, intense training sessions designed to prepare students for the practical reality of development,teaches programming skills and work on a wide variety of interesting and meaningful projects on a weekly basis.</p>
                    </div>
                    <div className="trainig-info">
                        <ul style={{color:'white'}}>
                            <li>1200Hrs of coding</li>
                            <li>100Hrs of soft skills training</li>
                            <li>500+ Git commits</li>
                        </ul>
                    </div>
                </div>
                <div className="about-img">
                    <div className="about-img-overlay">
                    <img src="../../Images/portImg.png" alt=""/>
                    </div>
                </div>
            </div>
        </Wrapper>
    )
}
