import Styled from "styled-components";
import { FaBars } from "react-icons/fa";
import React, { useEffect, useState } from "react";
import "./Navbar.css";
import { SideBar } from "./SideBar/SideBar";

const Brackets=Styled.span`
  color:grey;
  font-size:2rem;
  `
const Red=Styled.span`
  text-transform: lowercase; 
  font-size:2rem;

  & :nth-child(1){
    background-image: linear-gradient(to right top, #0264f7, #009fff, #00c4e7, #00df86, #a8eb12);
    background-size: cover;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
  }
  & :nth-child(2){
    color:green;
  }
  & :nth-child(3){
    color:yellow;
  }
  & :nth-child(4){
    color:blue;
  }
  & :nth-child(5){
    color:pink;
  }
  & :nth-child(6){
    color:cyan;
  }
  & :nth-child(7){
    color:lightblue;
  }

  & :nth-child(8){
    color:orange;
  }
`
const NavbarWrapper=Styled.div`
    transition: all .5s;
    ${(props)=>(props.hidden&&`
    background-color: rgba(255, 255, 255, .15);  
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, .15);
    `)}
`
export const Navbar = ({hidden}) => {
  const [open, setOpen] = useState(false);
  const handleSidebar = () => {
    setOpen(true);
  };
  const handleClose=()=>{
    setOpen(false)
  }
  return (
    <>
      <NavbarWrapper className="navbar-wrapper"  hidden={hidden}>
        <a className="navbar-links" href="/">
          <Brackets>{`</`}</Brackets>
            <Red>
              <span>s</span>
              <span>r</span>
              <span>i</span>
              <span>n</span>
              <span>i</span>
              <span>w</span>
              <span>a</span>
              <span>s</span>
            </Red>
          <Brackets>{`>`}</Brackets>
        </a>
        <span onClick={() => handleSidebar()}>
          <FaBars className="navbar-bar" />
        </span>
        <div className="navbar-bar-menu">
          <a href="#about" className="navbar-links">
            About
          </a>
          <a href="#project" className="navbar-links">
            Projects
          </a>
          <a href="#contact" className="navbar-links">
            Contact
          </a>
          <a className="navbar-bar-resume-button" href="https://drive.google.com/file/d/1LiqHjYiA11gChmY8x0qO2C95nIuyjc1H/view">
            Resume
          </a>
        </div>
      </NavbarWrapper>
        {open && <SideBar handleClose={handleClose} open={open}/>}
    </>
  );
};
