import React from 'react'
import "./ImageSection.css"
import Styled from "styled-components"

const Wrapper = Styled.div`
padding:13rem 10rem 10rem 10rem;
width:100%;
transition:all .5s ease-in-out;
@media only screen and (max-width: 834px) {
       display:grid;
        place-items:center;
        padding:10rem 7rem 10rem 7rem;
        transition:all .5s ease-in-out;
    }
    @media only screen and (max-width: 400px) {
        padding:10rem 0rem 10rem 1rem;
        white-space: wrap;
        transition:all .5s ease-in-out;
    }
`

export const ImageSection = () => {
    return (
        <Wrapper>
            <div className="wrapper-intro">
                <div className="wrapper-intro-top">
                    <div className="wrapper-margin">
                        <span className="intro-greeting">Hi, my name is</span>
                    </div>
                    <div className="wrapper-margin">
                        <span className="intro-name">Lanka Sriniwas.</span>
                    </div>
                    <div className="wrapper-margin">
                        <span className="intro-desc">Full Stack Developer. <br /> I build things for the web.</span>
                    </div>
                    <div className="intro-mail">
                        <button >
                            <a  href="mailto:lsriniwas20@gmail.com">
                            Get In Touch
                            </a>
                            </button>
                    </div>
                </div>
            </div>
        </Wrapper>
    )
}
