import React from 'react'
import Styled from "styled-components"

const Wrapper=Styled.div`
    border:1px solid white;
    height:50px;
    cursor:pointer;
    width:50px;
    border-radius:50%;
    position:fixed;
    right:20px;
    bottom:50px;
    color:#fff;
    font-Size:14px;
    display:grid;
    place-items:center;
    place-content:center;
`

export const Fab = ({handleChange}) => {
    return (
        <Wrapper onClick={handleChange}>
            <div>
            Change
            </div>
        </Wrapper>
    )
}
